@use 'global' as *;

.btn {
  background: transparent;
  padding: rem(24) rem(80);
  border: 1px solid $accent-color;
  box-sizing: border-box;
  border-radius: 75px;
  position: relative;
  z-index: 1;
  display: inline-block;
  transition: .5s;
  color: $base-color;
}

.btn--red {
  border: 1px solid $accent2-color;
}

.btn--sp {
  padding: 0;
}

.btn::before,
.btn::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 75px;
}

.btn::before {
  background: $accent-color;
  z-index: -1;
  transition: transform ease .5s, opacity .5s;
}

.btn--red::before {
  background: $accent2-color;
}

.btn::after {
  border: 1px solid $accent-color;
  box-sizing: border-box;
}

.btn--red::after {
  border: 1px solid $accent2-color;
}

.btn:hover {
  color: $accent-color;                                                          
}

.btn--red:hover {
  color: $accent2-color;
}

.btn--sp:hover {
  background-color: $base-color;
}

.btn:hover::before {
  transform: scale(0);
  opacity: 0;
}

.btn--sp,
.btn--sp::before,
.btn--sp::after {
  border-radius: 0;
}


